@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
a {
  color: steelblue;
  text-decoration: none;
}

a:hover{
  text-decoration: none;
  text-shadow: 1px 1px silver;
}

.user-row {
  padding: 15px;
}

.user-login-container {
  padding: 10px 10px 10px 0px;
}

.user-login-text {
  color: steelblue;
  font-family: 'Open Sans', sans-serif;
  font-weight: bolder;
  text-shadow: 2px 1px lightgray;
}

.user-details {
  font-weight: bold;
  text-align: right;
}

.user-date-joined-text {
  font-style: italic;
  font-weight: bolder;
}

.user-avatar-container {
  display: flex;
  justify-content: center;
}

.avatar {
  border-radius: 100px;
  box-shadow: 5px 5px lightsteelblue;
  height: 125px;
  margin: 15px;
  width: auto;
}

.user-bio-container {
  align-items: center;
  display: flex;
  font-size: 1.2em;
}

.user-followers-container {
  padding-top: 25px;
  text-align: center;
}

.user-followers-text-label {
  font-style: italic;
  font-weight: bolder;
}

.user-followers-text {
  color: darkred;
  font-family: 'Open Sans', sans-serif;
  font-weight: bolder;
}

.user-button {
  font-family: 'Open Sans', sans-serif;
  font-weight: bolder;
}

.link {
  color: steelblue;
  text-decoration: none;
}

.link:hover{
  text-decoration: none;
  text-shadow: 1px 1px silver;
}

.repo-row {
  padding: 15px;
}

.repo-name-text {
  color: steelblue;
  font-family: 'Open Sans', sans-serif;
  font-weight: bolder;
}

.repo-details {
  font-weight: bold;
  text-align: right;
}

.repo-created-text {
  font-style: italic;
  font-weight: bolder;
}

.repo-avatar-container {
  align-items: center;
  display: flex;
  justify-content: center;
}

.avatar {
  box-shadow: 8px 8px lightsteelblue;
  border-radius: 100px;
  height: 125px;
  margin: 5px;
  width: auto;
}

.repo-description-container {
  align-items: center;
  display: flex;
  font-size: 1.2em;
}

.repo-description-text {
  font-weight: bolder;
}

.repo-stars-container {
  padding-top: 25px;
  text-align: center;
}

.repo-stars-text-label {
  font-style: italic;
  font-weight: bold;
}

.repo-stars-text {
  color: darkred;
  font-family: 'Open Sans', sans-serif;
  font-weight: bolder;
}

.repo-button {
  font-family: 'Open Sans', sans-serif;
  font-weight: bolder;
}

.table {
  background: rgb(0,0,0);
  background: radial-gradient(circle, rgba(0,0,0,0.1) 75%, rgba(0,0,0,.15) 0%, rgba(0,0,0,0.2) 100%);
  border: gray solid 2px;
  border-radius: 10px;
  box-shadow: 5px 5px lightgray;
  font-family: 'Open Sans Condensed';
  width: auto;
}

.header-row {
  border-bottom: 2px solid gray;
  color: teal;
  display: flex;
  font-family: 'Open Sans', sans-serif;
  font-weight: bolder;
  justify-content: center;
  padding-bottom: 10px;
  padding-top: 15px;
  text-shadow: 2px 1px lightsteelblue;
}

.table-title {
  font-weight: bolder;
}

.table-data {
  font-weight: bold;
}

.top-data{
  background: silver;
  font-weight: bolder;
}

.dashboard-container {
  background-color:  white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 25px;
  padding: 50px;
  width: 67vw;
}
.dashboard-row {
  display: flex;
  font-family: 'Open Sans', sans-serif;
  font-weight: bolder;
  justify-content: center;
  margin-top: 50px;
}

.app {
  background: lightgray;
  display: flex;
  justify-content: center;
  width: 100vw;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

